import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { TransitionablePortal,Segment,Icon,Button } from 'semantic-ui-react'

const Cart = ({cart,lan}) => {
  const iState = {
    ui: {
      en:['Order',''],
      es:['Ordenar',''],
      bg:['ะะพัััะฐะน',''],
    }
  }

  //const [cartOpen,openCart] = useState(!!cart.length)
  const s = { height: (cart.length*7).toString().concat('vh') }
  let total = cart.length===0? 0 : cart.map( i=>i.price*i.quant ).reduce( (a,c)=>(parseFloat(a)+parseFloat(c)) ).toFixed(2)

  return (

      <TransitionablePortal
        onShow={()=>null}
        open={cart.length>0}
        closeOnDocumentClick={false}
        transition={{animation:'fly up', duration: 800}}
      >
      <Segment basic className='cart-bottom'>
        <ul>
          {cart.map( (i,ind) =>{
            return (<li>
              <div className='ui grid item'>
                <div className='six wide column nopad'>{i.name}</div>
                <div className='three wide column nopad items'><Icon name='x' size='tiny' />{i.quant}</div>
                <div className='three wide column nopad'>{(i.price*parseFloat(i.quant)).toFixed(2)}</div>
                <div className='two wide column nopad'>
                  <span><Icon name='minus circle' /></span>
                </div>
              </div>
            </li>)
          })}
        </ul>
        <Button
          fluid
          as={Link} to='/cart'
          width={8}
          disabled={cart.length === 0 || total<1 }
          color='orange'
          className='submit-but'
          content={ iState.ui[lan][0].concat(': ', total.toString())}
          onClick={() => {}/*addToCart(cart).then( ()=>{ history.push('/order') } )*/}
        />
      </Segment>
      </TransitionablePortal>

  )
}

const mapStateToProps = state => ({
  cart: state.cart
})

export default connect(mapStateToProps)(Cart)
