import React from 'react'
import { Form,Button,Message,Checkbox } from 'semantic-ui-react'

export default class UserDoor extends React.Component {
  state = {
    ui: {
      en: ['floor','door','entry','bell','mobile','myHome, theOffice,...','Name it like:','Primary'],
      es: ['piso','puerta','entrada','timbre','mobile','miCasa, LaOficina,...','Ponle nombre:','Principal'],
      bg: ['ะตัะฐะถ','ะฒัะฐัะฐ','ะฒัะพะด','ะทะฒัะฝะตั','ะผะพะฑะธะปะตะฝ','ัะะฐั, ะพัะธัะฐ, ะฟัะธะะฐะผะฐ,...','ะะผะตะฝัะฒะฐะน ะะดัะตัะฐ:','ะัะฝะพะฒะตะฝ']
    },
    valid: true,
    empty: true,
    data: {},
  }

  componentDidMount() {
    if(this.props.edit){
      this.setState({
      data: {...this.state.data, prime: this.props.loc.prime}
    })}
  }

  onChange = (e,{name,value}) => {
    const { edit,onInput,addData } = this.props
    let v = name === 'floor'? Number(value) : value
    this.setState({
      data: {...this.state.data, [name]: v}
    })
    if(edit){
      onInput(name, v)
    } else {
      addData({[name]: v})
    }
  }

  onCheckBox = (e, {name}) => {
    const { data } = this.state
    this.props.onInput(name, data[name]? Number(!data[name]) : 1)
    this.setState({
      data: {...data, [name]: data[name]? !data[name] : true}
    })
  }

  onClear = e => {
    e.preventDefault()
    this.setState({
      data: {}
    })
  }

  render() {
    const {lan,edit,loc} = this.props
    const ui = this.state.ui[lan]
    const { valid,data } = this.state
    return (
        <Form>
          <Form.Group>
            <div className='ui grid'>

              <div className="row">
                <div className='eight wide column rect'>
                  <Form.Input onChange={this.onChange}
                    required
                    name='bell'
                    placeholder={edit && loc.bell? ui[3].concat(': ',loc.bell) : ui[3]}
                    value={data.bell || ''}
                  />
                </div>
                <div className='seven wide column rect'>
                  <Form.Input onChange={this.onChange}
                    required
                    name='mobile'
                    placeholder={edit && loc.mobile? loc.mobile : ui[4]}
                    value={data.mobile || ''}
                  />
                </div>
              </div>

              <div className="row">
                <div className='four wide column rect'>
                  <Form.Input onChange={this.onChange}
                    required
                    name='floor'
                    placeholder={edit && loc.floor? loc.floor.toString().concat(' ',ui[0]) : ui[0]}
                    value={data.floor || ''}
                  />
                </div>
                <div className='four wide column rect'>
                  <Form.Input onChange={this.onChange}
                    required
                    name='door'
                    placeholder={edit && loc.door? loc.door.toString().concat(' ',ui[1]) : ui[1]}
                    value={data.door || ''}
                  />
                </div>
                <div className='four wide column rect'>
                  <Form.Input onChange={this.onChange}
                    name='entry'
                    placeholder={edit && loc.entry? loc.entry.toString().concat(' ', ui[2]) : ui[2]}
                    value={data.entry || ''}
                  />
                </div>
                {!edit && <div className='tree wide column'>
                  <Button
                    className='add-but'
                    icon='ban'
                    onClick={this.onClear}
                  />
                </div>}
              </div>

              {edit &&
                  <div className='row'>
                    <div className='eight wide column rect'>
                      <Form.Input onChange={this.onChange}
                        label={ui[6]}
                        name='name' placeholder={loc.name? loc.name : ui[5]}
                        value={data.name || ''}
                      />
                    </div>
                    <div className='seven wide column rect'>
                      <Checkbox
                        label={ui[7]}
                        name='prime'
                        checked={data.prime||loc.prime}
                        onClick={this.onCheckBox}
                      />
                    </div>
                  </div>
              }

            </div>

              { !valid &&
                <div className='margined full-width'>
                  <Message negative>
                    <Message.Header>{ui[3]}</Message.Header>
                    <p>{1}</p>
                  </Message>
                </div>
              }

          </Form.Group>
        </Form>
    )
  }
}
